<template>
  <el-form class="form" :inline="true" @submit.native.prevent="onSubmit" :model="form" label-width="100px">
    <el-breadcrumb separator="/" style="margin-bottom: 30px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/article/index">文章列表</el-breadcrumb-item>
      <el-breadcrumb-item>详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="24">
      <el-col>
        <el-form-item label="文章标题">
          <el-input v-model="form.title" placeholder="请输入文章标题"></el-input>
          <el-input v-model="form.title_en" placeholder="请输入文章标题(英文)"></el-input>
          <el-input v-model="form.title_yn" placeholder="请输入文章标题(越南)"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="文章排序">
          <el-input type="number" v-model="form.sort" placeholder="请输入文章排序"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="是否开启">
          <el-select size="small" v-model="form.is_disable" clearable placeholder="请选择">
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="内容" style="display: flex;">
          <vue-editor
            useCustomImageHandler
            @image-added="handleImageAdded"
            v-model="form.content"
            style="flex: 1;"
          ></vue-editor>
        </el-form-item>
        <el-form-item label="内容(英文)" style="display: flex;">
          <vue-editor
            useCustomImageHandler
            @image-added="handleImageAdded"
            v-model="form.content_en"
            style="flex: 1;"
          ></vue-editor>
        </el-form-item>
        <el-form-item label="内容（越南）" style="display: flex;">
          <vue-editor
            useCustomImageHandler
            @image-added="handleImageAdded"
            v-model="form.content_yn"
            style="flex: 1;"
          ></vue-editor>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'
import { VueEditor } from 'vue2-editor'
import { Message } from 'element-ui'

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      config: {
        cats: {},
      },
      form: {
        title: '',
        title_en: '',
        title_yn: '',
        content: '',
        content_en: '',
        content_yn: '',
        cid: 1,
        is_disable: 'Y',
        sort: 0,
      },
    }
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  mounted() {
    const id = this.$router.history.current.params.id

    Fetch('/article/art/config', { id }).then(r => {
      this.config.cats = r.data.cats
    })

    if (id > 0) {
      Fetch2('/article/art/edit/' + id).then(r => {
        this.form = r.data.view
      })
    }
  },
  methods: {
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      console.log(formData)
      formData.append('file', file)
      Fetch('/api/api/upload', formData).then(result => {
        let url = result.data.file
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      })
    },
    onSubmit() {
      const id = this.$router.history.current.params.id

      if (typeof id != 'undefined') {
        Fetch3('/article/art/edit/' + id, {
          id: this.$router.history.current.params.id,
          ...this.form,
        }).then(() => {
          Message.success('操作成功')
          this.$router.replace('/article/index')
        })
      } else {
        Fetch('/article/art/create', {
          id: this.$router.history.current.params.id,
          ...this.form,
        }).then(() => {
          Message.success('操作成功')
          this.$router.replace('/article/index')
        })
      }
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
